import React from 'react'

function Home() {
  const styles = {
    wrapper: {
      display:"flex",
      justifyContent:"center",
      alignItems:"center",
      height:"100vh",
      backgroundColor:"#f3e5d8",
      fontFamiy:"'poppins',sans-serif",
    },
    card: {
      backgroundColor: "#EAD4C0",
      border:"4px solid #A67B5B",
      borderRadius:"20px",
      padding:"40px",
      textAlign:"center",
      color:"4B2E2E",
      widht:"400px",
      boxShadow:"0 4px 15px rgba(0,0,0,1)",
      fontSize:"18px",
      fontWeight:"bold",
    },
    persegi:{
      widht:"100%",
      padding:"12px",
      margin:"10px 0",
      borderRadius:"12px",
      border:"2px solid #A67B5B",
      backgroundColor:"#DCC3A1",
      color:"#4B2E2E",
      fontSize:"16px",
      textAlign:"left",
      PointerEvent:"none",
    },
    tombol: {
      marginTop:"20px",
      padding:"12px 24px",
      backgroundColor:"#A67B5B",
      border:"none",
      borderRadius:"10px",
      color:"#FFFFFF",
      fontWeight:"bold",
      fontSize:"16px",
      boxShadow:"0 2px 10px rgba(0,0,0,1)",

    }


  }

return (
  <div style={styles.wrapper}>
      <div style={styles.card}>
        <h3>HAAYYOOOW</h3>
        <input
        style={styles.persegi}
        placeholder='Username'/>
        <input
        style={styles.persegi}
        placeholder='Password'
        type='Password'
        />
        <button style={styles.tombol}>Login</button>
      </div>
    </div>
  )
}

export default Home;